.mainContainer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: black;
  backdrop-filter: blur(0.05rem);
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-sizing: border-box;
  padding: 2rem;
}

.imageContainer {
  flex: 1 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  justify-content: center;
  background: black;
}

.imageContainer img {
  object-fit: contain;
  object-position: 50% 50%;
}
