.app {
  font-family: sans-serif;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  background: yellow;
  box-sizing: border-box;
  padding: 2rem;
}

.imageViewerContainer {
  height: 75vw;
  width: 75vw;
}

body {
  margin: 0;
}

h1,
h2 {
  margin: 0 0 1rem 0;
}

@media (orientation: landscape) {
  .imageViewerContainer {
    height: 50vh;
    width: 50vh;
  }
}
